(function() {
    'use strict';
    angular.module('core.utils').service('Newsletter', /*@ngInject*/ function($http, $page, $state, $q, api, Busy) {

        // var url = api.url + '/api/newsletter/';
        var url = 'http://pr-server.herokuapp.com/api/newsletter/';
        this.service = service;

        function service(params) {
            //
            // Default Params
            // 
            this.busy = new Busy.service();

            //
            // Instantiate
            // 
            params = params ? params : {};
            angular.extend(this, params);

        };

        this.service.prototype.create = create;
        this.service.prototype.formStatus = formStatus;
        this.service.prototype.negativePromise = negativePromise;

        function create() {
            // Are we busy?
            if (this.busy.start('create')) return this.negativePromise();

            // 
            // Validar email
            // 
            if (!this.email || !validateEmail(this.email)) {
                $page.toast('Insira um email válido!');
                this.busy.end('create');

                return this.negativePromise();
            }

            //
            // Limpar o model e separar outros dados
            // 
            var model = cleanModel(this);

            return $http
                .post(url, {
                    model: model
                })
                .success(
                    //
                    // success
                    //
                    function(response) {
                        $page.toast('Email cadastrado, você receberá as opções de pagamento em breve!');
                        this.busy.end('create');

                        //
                        // Limpar o form
                        // 
                        this.email = '';
                        
                        return response;
                    }.bind(this))
                .error(
                    //
                    // fail
                    //
                    function(response) {
                        $page.toast(response && response.error ? response.error : 'não foi possível cadastrar o email, tente novamente!');
                        this.busy.end('create');

                        return response;
                    }.bind(this));
        }

        function formStatus(method, invalid) {
            return (this.busy.status(method) || invalid);
        }

        function negativePromise() {
            // Start promise
            var defer = $q.defer();

            // Reject promise
            defer.reject(false);

            // Return promise
            return defer.promise;
        }

        function cleanModel(model) {
            if (!model) return model;

            var newModel = $.extend({}, model);

            newModel.busy = undefined;
            newModel.captcha = undefined;

            var clone = $.extend({}, newModel);
            return clone;
        }

        function validateEmail(email) {
            var re = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i;
            return re.test(email);
        }
    });
})();
